  .toolbar {
    position: fixed;
    background-color: rgba(0,0,0,.0);    
    min-height: 4vh;
    height: 10vh;
    top: 0;
    color: white;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    box-shadow: #252325 0px 1px 15px;
    transition: height 0.7s;
    z-index: 10;
    min-width: 100%;
    font-family: 'Montserrat', sans-serif;    
    border-bottom: #6CADDF solid .001rem;

    .Name-text {
      font-size: calc(12px + 3vmin);
      text-align: left;
      width: 50%;
      color: #fff;
      margin: 0;
      font-weight: 400;
      font-family: 'Great Vibes', sans-serif;
      letter-spacing: 3px;
    }

  .sections {
    display: flex;
    flex-direction: row;
    width: 40%;
    font-size: calc(2px + 2vmin);
    align-items: center;
  
    a {
        flex-basis: 40%;
        cursor: pointer;
        color: #fff;
        text-decoration: none;
        text-align: center;
    }
  
    a:hover {
        font-weight: 700;
    }
  }
}

.toolbar.active {
  height: 7vh;
  background-color: rgba(0,0,0,.9);

  .Name-text {
    font-size: calc(7px + 3vmin);
  }
}

.List {
  display: none;
}

.List.active {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  padding-bottom: 40rem;
  background-color: rgba(0,0,0,.8);
  position: fixed;
  align-items: center;
  z-index: 1;
  animation: fadeIn 1s;

  .List-child-top {
    padding-top: 9rem;
    width: 100%;

    a {
      text-decoration: none;
    }
  }

  .List-child {
    flex-basis: 100%;
    padding-top: 3rem;
    width: 100%;

    a {
      text-decoration: none;
    }
  }

  .List-child h2:hover {
    font-weight: 900;
    animation: fadeIn .5s;
  }

  .List-child-top h2:hover {
    font-weight: 900;
    animation: fadeIn .5s;
  }

  h2 {
      width: 100%;
      color: #fff;
      text-decoration: none;
  }
}

@media (max-width: 40em) {
  #Menu {
    margin-left: 6rem;
  }

  .List.active {
  
    h2 {
        margin-bottom: 0rem;
    }
  }

  #Hide-on-mobile {
    display: none;

  }

  .toolbar .Name-text {
    margin-inline: .7rem;
  }
}

@keyframes fadeIn {
  0% { opacity: 0; }
  100% { opacity: 1; }
}